.SearchContainer {
  top: 0;
  left: 0;
  position: sticky;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding: 12px 24px;
  background-color: #fca311;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
  0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.SearchForm {
  display: flex;
  align-items: center;
  height: 48px;
  width: 70%;
  max-width: 90%;
  margin: 0 auto;
  background-color: #ffcd00;
  border-radius: 5px;
  overflow: hidden;
}

.SearchForm-button {
  display: inline-block;
  background-color: #ff8303;
  width: 48px;
  height: 48px;
  border: 0;
  background-image: url('https://cdn-icons-png.flaticon.com/512/149/149852.png');
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.8;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.SearchForm-button:hover {
  opacity: 1;
}

.SearchForm-button-label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

.SearchForm-input {
  display: inline-block;
  width: 100%;
  padding: 8px 12px;
  font: inherit;
  font-size: 18px;
  border: none;
  outline: none;
}

.SearchForm-input::placeholder {
  font: inherit;
  font-size: 16px;
  opacity: 0.7;
}
