.ImageGalleryItem {
  border-radius: 3px;
}

.ImageGalleryItem-big {
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem-big:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}

.ImageGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 16px;
  margin-bottom: 16px;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}
